import React from 'react'
import { Component } from 'react'
import './AddAnime.css'
class  AddAnime  extends Component{
state = {
        touched: false,
        isValid: false,
        errMsg: '',
    }

addAnimeHandler = (e) => {

    let touched = {...this.state.touched}
    let isValid = {...this.state.isValid}
    let errMsg = {...this.state.errMsg}
    touched = true;

    const val = e.target.value.trim();

    if(val.length <= 0){
        isValid = false;
        errMsg = 'Your form is not complete'
    }else{
        isValid = true;
        errMsg = ''
    }
    this.setState({
        touched: touched,
        isValid: isValid,
        errMsg:errMsg,

    })
    console.log(this.state.isValid);
    console.log(this.state.errMsg);
    console.log(this.state.touched);
}

subHandler = () => {
    if(this.state.isValid === true){
        console.log(this.props);
    }
}

    render (){
        return (
        <div className="add-anime">
            <div className="container">
                <h2>add anime</h2>
                {
                    this.state.isValid ? null : (<h6 className="text-danger" style={{textAlign:'center'}}>{this.state.errMsg}</h6>)
                }
                <div className="row100">
                    <div className="col">
                        
                        <div className="inputBox">
                            <input type="text" onBlur={this.addAnimeHandler} name="name" required="required"/>
                            <span className="text">Name</span>
                            <span className="line"></span>

                        </div>
                    
                    </div>
                    <div className="col">
                        <div className="inputBox">
                            <input type="text" onBlur={this.addAnimeHandler} name="genres" required="required"/>
                            <span className="text">genres</span>
                            <span className="line"></span>
                            
                        </div>
                    </div> 
                </div>

                <div className="row100">
                    <div className="col">
                        <div className="inputBox">
                            <input type="text" onBlur={this.addAnimeHandler} name="rank" required="required"/>
                            <span className="text">rank</span>
                            <span className="line"></span>
                        </div>
                    </div>  
                    <div className="col">
                        <div className="inputBox">
                            <input type="text" onBlur={this.addAnimeHandler} name="score" required="required"/>
                            <span className="text">score</span>
                            <span className="line"></span>
                        </div>
                    </div> 
                    <div className="col">
                        <div className="inputBox">
                            <input type="text" onBlur={this.addAnimeHandler} name="episodes" required="required"/>
                            <span className="text">episodes</span>
                            <span className="line"></span>
                        </div>
                    </div> 
                    <div className="col">
                        <div className="inputBox">
                            <input type="text" onBlur={this.addAnimeHandler} name="seasons" required="required"/>
                            <span className="text">seasons</span>
                            <span className="line"></span>
                        </div>
                    </div>
                </div>
                <div className="row100">
                    <div className="col">
                        <div className="inputBox">
                            <input type="text" onBlur={this.addAnimeHandler} name="trailer" required="required"/>
                            <span className="text">trailer</span>
                            <span className="line"></span>
                        </div>
                    </div>
                    <div className="col">
                        <div className="inputBox">
                            <input type="text" onBlur={this.addAnimeHandler} name="link" required="required"/>
                            <span className="text">link</span>
                            <span className="line"></span>
                        </div>
                    </div> 
                </div>
                <div className="row100">
                    <div className="col">
                        <div className="inputBox textarea">
                            <textarea onBlur={this.addAnimeHandler} required="required"></textarea>
                            <span className="text">story</span>
                            <span className="line"></span>
                        </div>
                    </div>
                </div>
                <div className="row100">
                    <div className="col">
                        <input type="submit" value="Add" onClick={this.subHandler}/>
                    </div>
                </div>

            </div>
        </div>
    )}
    
}

export default AddAnime
